.green-plus {
    height: 64px;
    width: 64px;
}
.green-plus .frame {
    background-color: #7a8b99;
    border: 1px solid;
    border-color: #a9ddd6;
    border-radius: 32px;
    height: 64px;
}
.green-plus .overlap {
    height: 50px;
    left: 11px;
    position: relative;
    top: 15px;
    width: 42px;
}
.green-plus .text-wrapper {
    color: #a9ddd6;
    font-family: "Montserrat";
    font-size: 10px;
    font-weight: 400;
    left: 0;
    letter-spacing: 0;
    line-height: 22px;
    position: absolute;
    text-align: center;
    top: 24px;
    white-space: nowrap;
    width: 42px;
}
.green-plus .group {
    height: 28px;
    left: 7px;
    position: absolute;
    top: 0;
    width: 28px;
}
.green-plus .overlap-group {
    height: 28px;
    position: relative;
}

.green-plus .rectangle {
    height: 28px;
    left: -2px;
    position: absolute;
    top: 0;
    width: 30px;
}

.green-plus .div {
    background-color: #a9ddd6;
    height: 28px;
    left: 13px;
    position: absolute;
    top: 0;
    transform: rotate(90deg);
    width: 2px;
}
